.footer-container {
  background-color: #46737A;
  padding: 20px;
  
  .fact-sheet {
    color: #F2FAF9;
    max-width: 960px;
    margin: auto;

    .title {
      font-size: 18px;
      font-weight: 600;
      margin-bottom: 10px;
    }

    p {
      margin: 0;
      font-size: 12px;
    }
  }

  .contact {
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    @media screen and (max-width: 576px) {
      margin-top: 20px;
      align-items: flex-start;
    }
  }

  a {
    color: #F2FAF9;
  }
}