.main {
  background-color: rgb(54, 161, 151);
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: sans-serif;

  .container {
    max-width: 960px !important;
  }
}


.subject-container{
  padding-top: 40px;
  position: relative;

  p {
    margin: 0;
  }

  button {
    border: none;
    background-color: transparent;

    &:focus,
    &:active {
      outline: none;
    }
  }

  input {
    flex: 1;
    border: none;
    background-color: transparent;
    border-bottom: 2px solid #354F53;
    font-size: 24px;
    font-weight: 600;
    border-radius: 0;

    @media screen and (max-width: 576px) {
      font-size: 18px;
    }

    &:active,
    &:focus {
      outline: none;
      box-shadow: none;
      border-color: initial;
    }

    &:disabled {
      background-color: transparent;
    }

    &::placeholder {
      color: #C8E0E4;
    }
  }

  .experienced {
    position: absolute;
    z-index: 99;
    top: 0;
    left: 0;
    height: 300px;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  .module-card {
    border-radius: 20px;
    background-color: white;
    padding: 30px 50px !important;
    margin: 10px 0;
    border-width: 2px;
    border-style: solid;
    border-color: transparent;
    transition: all 0.3s ease;

    @media screen and (max-width: 576px) {
      padding: 10px 20px !important;
    }

    &:hover {
      box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
      border-color: #46737a;
    }

    .top {
      .subject-type {
        color: dimgray;
        font-weight: 800;
        font-size: 32px;
        flex: 1;
        display: flex;
        align-items: center;

        @media screen and (max-width: 576px) {
          font-weight: 800;
          font-size: 18px;
        }
      }

      .delete-card {
        transition: all 0.3s ease;
        i, img {
          color: #46737a;
          font-size: 32px;
          transition: all 0.3s ease;
        }

        &:hover {
          span {
            color: #354F53;
          }
        }

        &:active {
          transform: scale(1.2);
        }
      }
    }

    .mid {
      width: 100%;
      
      .table {
        margin: 50px 0;
        th,
        td {
          text-align: center;
          padding: 5px;

          &:nth-of-type(1) {
            text-align: left;
          }

          @media screen and (max-width: 576px) {
            padding: 0;
          }

          .nlevel-total {
            display: flex;
            justify-content: center;
            p {
              min-width: 50px;
              border-radius: 10px;
              border: 2px #ff6779 solid;
            }
          }

          .best-3 {
            display: flex;
            justify-content: center;
            p {
              min-width: 50px;
              background-color: #ffe4e7;
            }
          }
        }
      }

      .score-total {
        th,
        td {
          padding-top: 20px;
          font-size: 24px;
          font-weight: 800;
          color: #46737a;
        }
      }

      .cca {
        th,
        td {
          font-weight: 800;
          color: #46737a;
        }
      }
      .eligibility {
        text-align: center;
        font-size: 18px;
        font-weight: 800;
        color: #46737a;
        padding: 10px 0;
        margin: 5px 0;
        border-radius: 10px;
        &:hover {
          background-color: rgb(167, 187, 184);
          color: rgb(255, 255, 255);
        }
      }
    }

    .bottom {
      margin-top: 30px;
      .level-container {
        max-width: 220px;
        flex: 1;
        display: flex;
        justify-content: space-between;
        align-items: center;

        @media screen and (max-width: 576px) {
          flex: none;
          width: 100%;
          justify-content: flex-start;
          max-width: none;
        }

        .level-button {
          box-shadow: 0 25px 30px 0 rgba(0, 0, 0, 0.15);
          border-radius: 20px;
          padding: 10px 15px;
          transition: all 0.3s ease;
          border: 2px solid transparent;
          min-width: 100px;
          max-height: 48px;

          @media screen and (max-width: 576px) {
            padding: 5px 7px;
          }

          &:hover {
            border: 2px solid #46737a;
          }

          &:active {
            transform: scale(1.2);
          }
        }
        .level-button:nth-of-type(2) {
          margin-left: 10px;
        }
        .active {
          background-color: #00d7af;
          color: white;
        }
      }

      .dropdown-container {
        flex: 1;
        display: flex;
        justify-content: flex-end;
        margin-left: 10px;

        @media screen and (max-width: 576px) {
          flex: none;
          width: 100%;
          max-width: none;
          margin-top: 20px;
        }

        .dropdown {
          .dropdown-toggle {
            min-width: 100px;
            background-color: white;
            box-shadow: 0 25px 30px 0 rgba(0, 0, 0, 0.15);
            padding: 10px 20px;
            border: 2px solid #46737a;
            color: #46737a;
            font-weight: 800;
            transition: all 0.3s ease;
            @media screen and (max-width: 576px) {
              padding: 5px 7px;
            }
            
            &:hover,
            &:active,
            &:hover,
            &:focus {
              border: 2px solid #46737a;
              background-color: white;
              outline: none;
              box-shadow: 0 25px 30px 0 rgba(0, 0, 0, 0.15);
            }

            &:active {
              transform: scale(1.2);
            }
          }

          .dropdown-menu {
            button {
              &:active {
                background-color: #46737a;
              }
            }
          }
        }
      }

      .info-dropdown {
        @media screen and (max-width: 808px) {
          margin-top: 10px;
        }

        .inner-drop {
          &:nth-of-type(1) {
            margin-right: 10px;
          }
        }
      }
    }

    .error-string {
      margin-top: 20px;
      color: #ff6779;
      font-size: 20px;
      font-weight: 800;
    }

    .grade-text {
      color: #46737a;
      font-weight: 800;
      margin-top: auto;
      margin-bottom: auto;
      margin-right: 10px;
    }

    .info {
      margin-top: 10px;
      font-size: 18px;
      font-weight: 600;
    }

    .core {
      color: rgb(23, 143, 131) !important;
      input {
        color: rgb(23, 143, 131) !important;
      }
    }
  }

  .add-button-container {
    width: 100%;
    margin: 20px 0;

    .add-button {
      box-shadow: 0 25px 30px 0 rgba(0, 0, 0, 0.15);
      border-radius: 20px;
      width: 100%;
      height: 50px;
      background-color: #03c4a0;
      color: #F2FAF9;
      font-weight: 800;

      transition: all 0.3s ease;
      border: 2px solid transparent;

      &:hover {
        border: 2px solid #354F53;
      }

      &:active {
        transform: scale(1.2);
      }
    }
  }

  .submit-button-container {
    display: flex;
    justify-content: flex-end;
    margin: 20px 0 50px 0;
    @media screen and (max-width: 576px) {
      justify-content: center;
    }

    .submit-button {
      box-shadow: 0 25px 30px 0 rgba(0, 0, 0, 0.15);
      border-radius: 50%;
      width: 100px;
      height: 100px;
      background-color: #ff6779;
      color: #F2FAF9;
      font-weight: 800;

      transition: all 0.3s ease;
      border: 2px solid transparent;

      &:hover {
        border: 2px solid #354F53;
      }

      &:active {
        transform: scale(1.2);
      }
    }
  }

  .back {
    justify-content: flex-start;

    @media screen and (max-width: 576px) {
      justify-content: center;
    }
  }

  .others {
    .bottom {
      margin-top: 0;
    }
  }
}
